.host {
    padding: 10px;
    justify-content: space-evenly;
    display: inline-flex;
    flex-wrap: wrap;
    align-content: center;
}

.info-section {
    padding: 0 32px;
    text-align: center;
    background-color: white;
}
.info-title-content {
    margin: 64px 0 128px 0;
}
.info-title {
    font-family: 'Montserrat', sans-serif;
    font-size: 35px;
    font-weight: bold;
}
.info-title > span {
    border-bottom: 4px solid #9D2553;
}

.info-description {
    margin: 64px;
    text-align: center;
    color: rgb(77, 75, 75);
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    letter-spacing: .8px;
    line-height: 1.8rem;
}
/* Cards */
.infocardscontent {
    margin: 64px 0;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 14px;
}
.infocardscontentH {
    margin: 64px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 32px;
}
.info-cards {
    height: auto;
    border: 1px solid #d7d3d3;
    border-radius: 12px;
    background-color: white;
    padding: 20px 10px 0 20px;
    margin: 0 0 20px 0;
}
.info-card-title {
    margin: 32px 0 40px 0;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 28px;
    font-weight: bold;
    letter-spacing: .8px;
    text-align: left;
}
.info-card-description {
    margin: 24px;
    color: rgb(77, 75, 75);
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    letter-spacing: .8px;
    line-height: 1.5rem;
    text-align: left;
}
.info-card-icon {
    background-color:transparent;
    border-radius: 50px;
    width: 50px;
    height: 50px;
    align-content: center;
    align-items: center;
    margin: auto;
}
.info-fa-icon {
    color: black;
    font-size: 24px;
    width: 30px;
    height: 30px;
}
.hero-image2 {
    position: relative;
    width: 100%;
    height: 100%;
    opacity: 0.05;
}
.howeasy{
    margin-top: 100px;
    color: rgb(77, 75, 75);
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
}
.about-video-content {
    margin: 10px 0 10px;
    padding: 0 50px 10px 50px;
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
    
}
.reactPlayerStyle {
    border-radius: 16px;
    overflow: hidden;
    border: 0;


}
/* Responsive */
@media screen and (max-width: 1600px) {
    .info-title {
        font-size: 32px; 
        padding-bottom: 20px; 
    }
    .info-description {
        margin: 64px 32px;
    }
    .infocardscontent {
        grid-template-columns: repeat(3, 1fr);
    }
}
@media screen and (max-width: 1000px) {
    .info-title {
        font-size: 32px; 
        padding-bottom: 20px; 
    }
    .info-description {
        margin: 64px 32px;
    }
    .infocardscontent {
        grid-template-columns: repeat(2, 1fr);
    }
}
@media screen and (max-width: 700px) {
    .info-description {
        margin: 64px 8px;
    }
    .infocardscontent {
        grid-template-columns: repeat(1, 1fr);
    }
    .about-video-content {      
        padding: 0;
    }
}
