.navbarsection {
    padding: 0 50px;
    display: flex;
    justify-content:space-between;
    align-items: center;
    height: 120px;
    width: 100%;
}

.navbartitle a {
    text-decoration: none;
    color: #003C71;
    font-size: 40px;
    
}
.navbartitle {
    font-family: 'Montserrat';
    font-weight: normal;
}

.navbaritems {
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 32px;
    font-family: 'Montserrat', sans-serif;
    padding: 0 20px 16px 0;
    font-size: 30px;
}
.navbaritems > li {
    list-style-type: none;
}

.navbarlinks {
    color: black;

    text-decoration: none;
    font-family:'Montserrat';
    font-family:'Nunito Sans';
    font-size: 14px;
    
    font-weight: bold;
    letter-spacing: 1.5px;
}

.navbarlinks:hover {
    color: #0cc2ea;
}

.containerNav {
    height: 100px;
    width: 120px;
    opacity: 0.6;
    position: relative;
}

.smallestcircleNav {
    top: 28px;
    left: 38px;
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: #f00;
    border-radius: 20px;
    opacity: 0.6;
}


.smallercircleNav {
    top: 34px;
    left: 50px;
    position: absolute;
    width: 16px;
    height: 16px;
    background-color: mediumslateblue;
    border-radius: 30px;
    opacity: 0.8;
}

.smallcircleNav {
    top: 47px;
    left: 30px;
    position: absolute;
    width: 24px;
    height: 24px;
    background-color: olivedrab;
    border-radius: 70px;
    opacity: 0.8;
}

.circleNav {
    top: 42px;
    left: 65px;
    position: absolute;
    width: 34px;
    height: 34px;
    background-color: orange;
    border-radius: 40px;
    opacity: 0.6;
}


