/* Mobile Navbar */
.mobilenavbarlinks {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    font-size: 26px;
    gap: 24px;
    text-align: left;
    font-family: 'Montserrat', sans-serif;
}
    
.mobilenavbarlinks li a {
    text-decoration: none;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    font-size: 20px;
    letter-spacing: .8px;
    transition: color .3s ease-in-out;
}
.mobilenavbarlinks li a:hover {
    color: #0cc2ea;
    cursor:pointer;
}

.navbar-section {
    padding: 0 32px;
    display: flex;
    justify-content:space-between;
    align-items: center;
    height: 100px;    
}
.navbar-title a {
    text-decoration: none;
    color: #003C71;
    font-size: 40px;
    
}
.navbar-title {
    font-family: 'Montserrat';
    font-weight: normal;
}

.navbar-sign {
    color: #54de54;
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    font-weight: bold;
}
.navbar-items {
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 32px;
    font-family: 'Montserrat', sans-serif;
    padding-right: 20px;
    font-size: 20px;
}
.navbar-items > li {
    list-style-type: none;
}
.navbar-links {
    text-decoration: none;
    color: black;
    font-size: 20px;
    letter-spacing: .8px;
}
.navbar-links:hover {
    color: #0cc2ea;
}
.navbar-btn {
    padding: 14px 20px;
    color: white;
    border: 1px solid transparent;
    border-radius: 28px;
    outline: transparent;
    background-color: #1A8EFD;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: .8px;
    cursor: pointer;
    transition: all .4s ease;
}
.navbar-btn:hover {
    color: #1A8EFD;
    background-color: white;
    border: 1px solid #1A8EFD;
}
/* Hamburger Icon */
.mobile-nav {
    display: none;
}
.hamb-icon {
    width: 26px;
    height: 26px;
    cursor: pointer;
}
.hamb-icon:hover {
    color: #0cc2ea;
}
/* Mobile Navbar */
.mobile-navbar {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -100%;
    background-color: white;
    z-index: 20;
    justify-content: center;
    align-items: center;
    transition: left .5s ease-in-out;
}
.mobile-navbar-close {
    position: absolute;
    top: 28px;
    right: 28px;
}
.mobile-navbar-close .hamb-icon:hover {
    color: rgb(255, 22, 22);
}
.open-nav {
    left: 0;
}
.opennav {
    left: 120px;
}
.mobile-navbar-links {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    font-size: 26px;
    gap: 24px;
    text-align: left;
}
.mobile-navbar-links li a {
    text-decoration: none;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    font-size: 20px;
    letter-spacing: .8px;
    transition: color .3s ease-in-out;
}
.mobile-navbar-links li a:hover {
    color: #0cc2ea;
    cursor:pointer;
}
/* Responsive */
@media screen and (max-width: 1200px) {
    .navbar-btn, .navbar-items .navbaritems {
        display: none;
    }
    .mobile-nav {
        display: block;
    }
}

.mobile-popup {
    padding: 0 10px 10px 10px;
    border: 0.8px solid var(--app-color-gray-lighter);
    box-shadow: 0 5px 6px 0 rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    width: 97%;
    height: 100%;
    position: fixed;
    top: -100%;
    left: 0;
    background-color: white;
    z-index: 20;
    justify-content: center;
    align-items: center;
    transition: bottom 1.5s ease-in-out;
    
}
.open-popup {
    top: 10%;
}

.mobile-popup-close {
    position: absolute;
    top: 28px;
    right: 28px;
}

